.button {
    display: flex;
    height: 40px; padding: 0;
    background: #2f5178;
    border: none;
    outline: none;
    border-radius: 5px;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
}
.button:hover, .button:active { background: #404141;
}
.button_text,
.button_icon {
display: inline-flex;
align-items: center;
padding: 0 8px;
color: #fff;
height: 100%;
}
.button_icon {
font-size: 1.5em;
height: 36px;
width: 40px;
background: rgba(0, 0, 0, 0.1);
}

img{
    height: 100%;
    width: 100%;
}